import React from "react";

const EditRecipeForm = (props) => {
  const { editRecipe, recipe } = props;
  return (
    <form onSubmit={editRecipe}>
      <h5>Name</h5>
      <input type="text" name="recipe-name" readOnly value={recipe.name} />
      <div>
        <h5>Ingredients</h5>
        {recipe.ingredients.map((ingredient, i) => (
          <textarea
            onChange={props.addIngredient}
            name={'ingredient' + i}
            key={'ingredient' + i}
            className="txt-input-lg"
            type="text"
            defaultValue={ingredient}
          ></textarea>
        ))}
      </div>

      <div>
        <h5>Steps</h5>
        {recipe.steps.map((step, i) => (
          <textarea
            onChange={props.addStep}
            defaultValue={step}
            name={'step' + i}
            key={'step' + i}
            className="txt-input-lg"
            rows="2"
          ></textarea>
        ))}
      </div>

      <button className="btn">Save</button>
    </form>
  );
};

export default EditRecipeForm;
